.gutter {
  background-color: #ddd;
}

.gutter:hover {
  background-color: #c4e1a4;
}

#toolbar {
  padding-left: 3px;
  overflow: hidden;
}

#toolbar .fa {
  border: 1px solid #0000;
  padding: 2px 5px;
}

#about-icon {
  vertical-align: top;
  height: 20px;
}

#toolbar .fa:hover, #about-icon:hover {
  cursor: pointer;
  background-color: #999;
  border-color: #ccc;
}

.dividor {
  color: #ddd;
  margin: 0 6px;
  font-style: normal;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

h1, h2, h3, h4, h5, h6 {
  scroll-margin-top: 1px;
}

pre code.hljs {
  padding: 0;
}

.success, .info, .warning, .danger {
  border: 1px solid #0000;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 15px;
}

.success > p:last-child, .info > p:last-child, .warning > p:last-child, .danger > p:last-child {
  margin-bottom: 0;
